import * as React from 'react';
import { Slot } from '@radix-ui/react-slot';
import { cva, type VariantProps } from 'class-variance-authority';

import { cn } from '../../lib/utils';

const buttonVariants = cva(
  'inline-flex items-center justify-center whitespace-nowrap rounded-lg text-sm font-medium transition duration-300 focus-visible:outline-none focus-visible:ring-1 min-h-[42px]  h-[42px]  focus-visible:ring-ring disabled:pointer-events-none disabled:opacity-50',
  {
    variants: {
      variant: {
        rounded:
          'border border-[#A2999E] bg-background shadow-md hover:bg-accent hover:text-accent-foreground hover:bg-blue-light/25 rounded-full ',
        default: 'bg-primary text-primary-foreground shadow hover:bg-orange/90',
        destructive: 'bg-destructive text-destructive-foreground shadow-sm hover:bg-destructive/90',
        outline:
          'border border-[#A2999E] bg-background shadow-sm hover:bg-accent hover:text-accent-foreground hover:bg-blue-light/25',
        secondary: 'bg-secondary text-secondary-foreground shadow-sm hover:bg-secondary/80',
        ghost: 'hover:bg-accent hover:text-accent-foreground hover:bg-blue-light/25 ',
        link: 'text-primary underline-offset-4 hover:underline',
        orange: 'bg-orange-50 border border-[#886221] hover:bg-orange/90 px-4 py-2 text-black',
        custom:
          ' hover: p-2 py-3 text-black hover:bg-blue-light/25 hover:scale-110 transition duration-300',
        fPoints: 'bg-success/75 text-primary-foreground shadow hover:bg-success/50',
        disabled: 'disabled'
      },
      size: {
        default: 'h-9 px-4 py-2',
        sm: 'h-8 rounded-md px-3 text-xs',
        lg: 'h-10 rounded-xl px-8',
        icon: 'h-9 w-9',
        full: 'w-full h-full'
      }
    },
    defaultVariants: {
      variant: 'orange',
      size: 'default'
    }
  }
);

export interface ButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {
  asChild?: boolean;
}

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  ({ className, variant, size, asChild = false, ...props }, ref) => {
    const Comp = asChild ? Slot : 'button';
    return (
      <Comp className={cn(buttonVariants({ variant, size, className }))} ref={ref} {...props} />
    );
  }
);
Button.displayName = 'Button';

export { Button, buttonVariants };
